@import "vars";
@import "fonts";

// ---------------------------------------
// ------------Invoices list -------------
// ---------------------------------------
.invoices_section{
	.header{
		font-family: RobotoBold;
		font-size: 1.5em;
		margin-bottom: 20px;
		display: flex;
		flex-direction: row;
		align-items: center;
	}
	.header-selects{
		width: 20%;
	}
	.header-selects-week{
		float: left;
		margin-right: 20px;
	}
	.label_thead{
		color: grey;
		width: 100%;
		display: flex;
		flex-direction: row;
		padding: 5px 5px 5px 40px;
		margin-top: 20px;
		span{
			width: calc(100% / 4 - 5px);
		}
	}
	.invoices_list{
		width: 100%;
		li{
			margin-bottom: 3px;
			background-color: white;
			display: flex;
			flex-direction: row;
			padding: 0 7px;
			align-items: center;
			border: 1px solid rgb(200, 200, 200);
			border-radius: 5px;
			transition: all .1s ease-in-out;
			.choice{
				width: 100%;
				display: flex;
				align-items: center;
				label{
					padding: 10px;
					width: 100%;
					margin-left: 5px;
					display: flex;
					flex-direction: row;
					justify-content: center;
					cursor: pointer;
					.field{
						width: calc(100% / 4);
					}
					.deliver_back{
						position: absolute;
						right: 50px;
						background-color: $color-success;
						color: white;
						padding: 3px 5px;
						border-radius: 3px;
						// top: 10px;
						margin-top: -2px;
					}
					.number_order{
						font-family: RobotoBold;
					}
				}
			}
			&:hover{
				background-color: $color-main;
				color: white;
			}
		}
	}
	.button_wrapper{
		text-align: right;
		margin-top: 10px;
	}
	&__back{
		font-size: 1.7em;
		padding-bottom: 10px;
		float: left;
	}
	.deliver{
		width: 100%;
		border: 1px solid rgb(180, 180, 180);
		border-radius: 5px;
		padding: 10px;
		display: flex;
		flex-direction: row;
		justify-content: space-between;
		height: 70px;
		align-items: center;
		margin-top: 5px;
		span{
			font-family: RobotoBold;
			font-size: 1em;
			margin: 10px 0;

		}
		.mb-4.form-floating{
			margin: 0 !important;
			height: 40px;
			.form-control{
				width: 100%;
				height: 40px;

			}
			label{
				padding: 10px;
			}
		}
		.button-wrapper{
			height: 40px;
			button{
				margin-right: 10px;
			}
			// width: 20%;
		}
	}

}


// ---------------------------------------
// ------------ Invoice ------------------
// ---------------------------------------
main{
	.Invoice-wrapper{
		position: relative;
		
		border-radius: 10px;
		.deliver-back-mark{
			padding: 5px 7px;
			background-color: $color-success;
			position: absolute;
			top: -40px;
			right: 0;
			color:white;
			border-radius: 3px;
		}
		.Invoice{
			width: 100%;
			@include bs(12px);
			padding: 30px 50px;
			border-radius: 10px;
			background-color: #fff !important;
			.header{
				display: flex;
				flex-direction: row;
				justify-content:space-between;
				background-color: #fff !important;
				.client_info{
					font-family: RobotoRegular;
					font-size: 0.7em;
					margin-bottom: 3px;
					background-color: #fff !important;
				}
			}
			.sub-header{
				margin: 40px 0;
				background-color: #fff !important;
				p{
					display: flex;
					flex-direction: row;
					justify-content: space-between;
					padding: 0 10%;
					.number_order, .date_order{
						// width: 50%;
						padding: 0 10px;
						// display: inline-block;
						// text-decoration: underline;
						border-bottom: 2px solid #000;
						text-align: center;
					}
				}
			}
			.prods{
				background-color: #fff !important;
				table{
					background-color: #fff !important;
					// border: 1px solid #000;
				}
			}
			.gost{
				margin: 40px 0;
				background-color: #fff !important;
			}
			.assembly-and-entering{
				background-color: #fff !important;
				width: 100%;
				// height: 200px;
				// display: flex;
				// flex-direction: column;
				// justify-content: right;
				.name-field{
					text-align: right;
					width: 100%;
					// display: inline-block;
					display: flex;
					flex-direction: row;
					justify-content: flex-end;
					margin-top: 10px;
					align-items: center;
				}
				.field{
					background-color: #fff !important;
					// width: 30%;
					// display: inline-block;
					text-align: center;
					// border-bottom: 1px solid #000;
					margin-left: 10px;
					display: flex;
					flex-direction: row;
					select{
						margin-right: 10px;
					}
				}
			}
			.write-for-client{
				width: 100%;
				margin-top: 40px;
				&-data{
					width: 100%;
					display: flex;
					flex-direction: row;
					justify-content: space-between;
					.info{
						width: 100%;
						text-align: center;
						margin-top: 20px;
						.field{
							width: 30%;
							border-bottom: 1px solid #000;
							display: inline-block;
						}
					}
					
				}
			}
			.signature{
				width: 100%;
				text-align: center;
				margin-top: 40px;
				.field{
					width: 20%;
					display: inline-block;
					border-bottom: 1px solid #000;
				}
			}
			.table-bottom-panel{
				margin-top: 20px;
				display: flex;
				width: 100%;
				justify-content: center;
				gap: 10px;
			}
		}
		.panel-bottom{
			margin-top: 20px;
			width: 100%;
			display: flex;
			justify-content: flex-end;
			button{
				margin-left: 10px;
			}
		}
	}
	.panigation{
		display: flex;
		justify-content: space-between;
		margin: 20px 0;
	}

}