@import "fonts";

// Colors
$color-main: #014cbd;
$black: rgb(36, 36, 36);
$color-bg-ligth: #202022;
$color-bg-dark: #161719;
$color-bg-grey: #444748;
$color-bg-mark: #db4f4f;
$color-success: #198754;

// mixins
@mixin trs {
	transition: all .2s ease-in-out;
}

@mixin button_default {
	display: inline;
	
	font-family: RobotoBold;
	background-color: transparent;
	border: 3px solid $color-main;
	padding: 7px 10px;
	border-radius: 20px;
	@include trs;
	box-shadow: 0 0 5px $color-main_;
	color: white;
	outline: none;
	cursor: pointer;

	&:hover{
		background-color: $color-main__;
		/* border: 3px solid $color-bg-mark__; */
	}
}

@mixin bs($size) {
	box-shadow: 0px 0px $size 0px rgba(34, 60, 80, 0.3);
}

.dfrowaic{
	display: flex;
	flex-direction: row;
	align-items: center;
}