@import "vars";
@import "fonts";

main{
	width: 100%;
	height: auto;
	margin-top: 70px;
	
	padding: 40px;
	.Order_wrapper{
		width: 100%;
		display: flex;
		justify-content: center;
		.Order{
			width: 100%;
			padding: 25px;
			// background-color: red;
			@include bs(20px);
			border-radius: 15px;
			// display: inline-block;
			display: flex;
			justify-content: center;
			align-items: center;
			flex-direction: column;
			.mb-12.form-floating{
				width: auto;
			}
			.top_panel{
				display: flex;
				flex-direction: row;
				.order_form_user{
					margin-right: 30px;
					
					.mb-12.form-floating{
						// display: flex;
						// justify-content: center;
						label{
						padding: 10px;
						}
					}
				}
				.order_form_additional{
					.within{
						display: flex;
						flex-direction: row;
						select{
							height: 40px;
							margin-right: 10px;
						}
					}
					.select_site{
						height: 40px;
						margin-bottom: 10px;
						width: 295px;
					}
					.form-control{
						font-family: RobotoBold;
						margin-bottom: 10px;
						height: 40px;
						width: 295px;
					}
					.within_input{
						width: 95px;
						margin-right: 10px;
					}
					label{
						padding: 10px 10px;
					}
				}
			}
			.additional_info{
				padding: 15px 0;
				.form-check{
					display: flex;
					flex-direction: row;
					align-items: center;
					margin-bottom: 10px;
					.form-check-label{
						font-family: RobotoBold;
						// margin-bottom: 10px;
						margin-left: 5px;
						width: 350px;
						display: flex;
						justify-content: space-between;
						align-items: center;
						flex-direction: row;
						flex-wrap: nowrap;
						input{
							margin-right: 7px;
							float: left;
							height: 30px;
							width: 150px;
						}
					}
					.price_grey{
						color: grey;
						margin-left: 15px;
						font-family: RobotoRegular;
						display: flex;
						align-items: center;
					}
				}
			}
			h2{
				font-family: RobotoBlack;
				font-size: 1.2em;
				color: $black;
				margin: 20px 0;

				text-align: center;
			}
			h1{
				margin: -70px 0 60px 0;
				font-family: RobotoBlack;
				font-size: 1.5em;
				color: $black;
				text-align: center;
			}
			.order_form_user{
				.form-control{
					width: 500px;
					height: 40px;
					margin-bottom: 10px;
					font-family: RobotoBold;
				}
				label{
					padding: 10px 10px;
				}
			}
			.order_panel{
				width: 100%;
				margin-top: 20px;
				padding: 20px;
				@include bs(10px);
				border-radius: 15px;
				display: block;
				position: relative;
				.mb-12.form-floating{
					margin-right: 7px;
					height: 40px;
					.form-control{
						height: 40px;
						margin-bottom: 10px;
						margin-right: 7px;
						font-family: RobotoBold;
					}
					label{
						padding: 11px 10px;
					}
				}
				

				.order_panel_select{
					width: auto;
					height: 40px;
					margin-right: 7px;
				}
				.oreder_panel_main_info{
					margin: 15px 0;
					display: flex;
					flex-direction: row;
					justify-content: flex-start;
					// background-color: red;
					.order_panel_id{
						padding: 12px 5px;
						font-family: RobotoBold;
					}
				}
				.order_panel_close{
					position: absolute;
					top: 5px;
					right: 10px;
					padding: 5px;
					cursor: pointer;
				}
	
				
				.apply_order{
					height: 40px;
				}
			}
			.button_add_order{
				width: 300px;
				margin-top: 20px;
			}
			.button_send_order{
				width: 40%;
				margin-top: 20px;
			}
			
		}
	}
	
}
