@import "fonts";
@import "vars";

#root{
	width: 100%;
	.sidebar{
		height: auto;
		min-height: 100vh;
		min-width: 20%;
		background-color: $color-main;
		.logo_wrapper{
			width: 100%;
			text-align: center;
			padding: 20px 0;
			a{
	
				text-decoration: none;
				.logo{
					font-family: RobotoBlack;
					color: white;
				}
			}
		}
		&_ul{
			margin-top: 20px;
			padding: 0;
			li{
				display: flex;
				flex-direction: row;
				align-items: center;
				a{
					color: white;
					text-decoration: none;
					width: calc(100%);
					// display: inline-block;
					display: flex;
					flex-direction: row;
					align-items: center;
					padding: 10px 10px 10px 20px;
					@include trs;
					&:hover{
						background-color: lighten($color-main, 10);
					}
					.icon_header{
						margin-right: 5px;
					}
				}
			}
		}
	}
}