@import "vars";
@import "fonts";

.main{
	width: 100%;
	height: 80%;
	display: flex;
	align-items: center;
	justify-content: center;
	.registration-form, .login-form{
		padding: 40px;
		border-radius: 15px;
		@include bs(15px);
		h2{
			text-align: center;
			color: #333;
			font-family: RobotoBold;
			font-size: 1.2em;
			margin: 20px 0;
		}
		.mb-12{
			height: 40px;
			margin-bottom: 10px;
			.form-control{
				height: 40px;

			}
			label{
				padding: 10px;
			}
		}
		select{
			margin-bottom: 10px;
		}
	}
}
